import React from 'react'
import { Select } from 'antd'
import { SelectValue } from "antd/lib/select";

const { Option } = Select;

const nums = new Set(["number", "integer"]);

/**
 * This is a silly limitation in the DOM where option change event values are
 * always retrieved as strings.
 */
function processValue(schema, value) {
  return value;
}

const AntdSelect = (props) => {
  const {
    options = {},
    enumOptions,
    uiSchema,
    updateItemData,
    validate,
    placeholder,
    formItemMeta,
    ...resetProps
  } = props;
  const { readonly = false } = uiSchema || {}
  const metaOptions = formItemMeta ? formItemMeta.getIn(["options", "widget", "select"]) : {}
  const { enumDisabled = [] } = options;
  const setDefaultProps = (): any => {
    const props: any = {};
    if (props.formItemData !== undefined) {
      props.defaultValue = props.formItemData;
    }
    return { options: props };
  }
  return (
    <Select
      disabled={readonly}
      placeholder={placeholder}
      {...setDefaultProps()}
      {...resetProps}
    >
      {enumOptions.map(({ value, label }, i) => {
        const disabled = enumDisabled && enumDisabled.indexOf(value) != -1;
        return (
          <Option key={i} value={value} disabled={disabled}>
            {label}
          </Option>
        );
      })}
    </Select>
  );
}

export default AntdSelect
