import React from 'react';
import { Icon, Modal, Button } from 'antd';
import { observer, inject } from 'mobx-react';

import { IconWrap } from './style'

const confirm = Modal.confirm;

const EditExtra: React.FC<any> = inject('schemaState', 'legao')(
  observer(store => {

    const { legao, schemaState, id } = store
  const onChange = () => {
    legao.change()
  };

    function showConfirm() {
      confirm({
        title: '提示',
        content: '确定删除该组件吗？',
        onOk() {
          const prevSchema = JSON.parse(JSON.stringify(schemaState.jsonState))
          const arr = id.replace(/_/g, "_properties_").split('_').slice(1)
          const lastOne = arr.pop()
          const property = arr.reduce((r, n, k) => {
            if (n !== 'properties') {
              eval(r).required = eval(r).required.filter(x => x !== n)
              eval(r).sortdata = eval(r).sortdata.filter(x => x !== n)
            }
            if (k === 0 && arr.length === 1) {
              prevSchema.sortdata = prevSchema.sortdata.filter(x => x !== n)
            }
            return `${r}["${n}"]`
          }, 'prevSchema')
          Reflect.deleteProperty(eval(property), lastOne)
          schemaState.update(prevSchema)
        },
        onCancel() { },
      });
    }

  return (
    <IconWrap>
      <Icon type="edit" theme="twoTone" onClick={onChange} />
      <Icon type="delete" theme="twoTone" twoToneColor="#ff0000" style={{ marginLeft: 10 }} onClick={showConfirm}/>
    </IconWrap>
  );
}))

export default EditExtra;
