import R from 'ramda'

export const isStringNull = (str: any): boolean => {
  return !str || !String(str).trim()
}

// 默认函数，返回第一个参数
export const noop = (...args: any[]) => args[0];

export * from './FormatSchema'

export const Trans_Map = new Map([
  ["component", "ui:widget"],
  ["className", "classNames"],
  ["condition", "condition"],
  ["sortdata", "ui:order"],
  ["props", "ui:options"],
  // ["layout", "ui:options"],
])

export const filterData = R.memoizeWith(R.identity, _ => _.filter(x => Trans_Map.has(x)))

export const separate = (source, path, data) => arr => {
  const parentName = path.pop()
  if (path[path.length - 1] === 'properties') {
    const LEN = path.length - 1
    // 处理required
    path.reduce((r, n, k) => {
      r[n] = r[n] || {}
      if (data.required && (!LEN || k === LEN - 1)) {
        if (!LEN) {
          r.required = r.required || []
          r.required.push(parentName)
        } else {
          r[n].required = r[n].required || []
          r[n].required.push(parentName)
        }
      }
      if (k === LEN) {
        r[n][parentName] = data
      }
      return r[n]
    }, source.schema)

    if (arr.length) {
      const formatArr = path.filter(x => x !== 'properties')
      let render = source.uiSchema
      formatArr.length && formatArr.reduce((r, n, k) => {
        r[n] = r[n] || {}
        // add properties 之前的那个
        if (k === formatArr.length - 1) {
          // render[`${Trans_Map.get('sortdata')}`] = r[n].sortdata
          render = r[n]
        }
        return r[n]
      }, source.uiSchema)
      arr.forEach((property) => {
        render[parentName] = render[parentName] || {}
        render[parentName][`${Trans_Map.get(property)}`] = data[property]
      })
    }
  } else {
    source.schema[parentName] = data
  }
}

export const pipeData = (source: object, path: Array<String>) => (data: object) => {
  return R.compose(separate(source, path, data), filterData, Object.keys)(data)
}

export const keyHandler = (obj, arr, fn) => Object.keys(obj).forEach(k => arr.push(k) && fn(obj[k]))

