import React from 'react';
import { observer, inject } from 'mobx-react';
import { observable } from 'mobx';
import { message } from 'antd';
import JsonEditor from '@/core/JsonEditor';

import { Content } from './style';

message.config({ maxCount: 1 });

const FormSchema: React.FC<any> = inject('schemaState')(
  observer((store: any) => {
    const changeHandler = source => {
      store.schemaState.update(observable(source));
    };
    return (
      <Content
        as={JsonEditor}
        source={store.schemaState[store.type]}
        onChange={changeHandler}
        isEdit={store.type === 'jsonState'}
      />
    );
  })
);

export default FormSchema;
