import React from 'react';
import { observer, inject } from 'mobx-react';
import { observable } from 'mobx';
import AntdForm from '@/core/antd';
import Field from './Field'


const log = type => console.log.bind(console, type);

const Form = inject('schemaState')(
  observer(store => {
    const changeHandler = (source) => {
      store.schemaState.getData(observable(source.formData));
    };


    return (
      <AntdForm
        {...store.schemaState.schemaTree}
        formData={store.schemaState.formData}
        FieldTemplate={Field}
        onChange={changeHandler}
        onSubmit={log('submitted')}
        onError={log('errors')}
      />
    )
  })
);

export default Form;
