import { observable, action, autorun, computed } from 'mobx';
import { pipeData, keyHandler, FormatSchema } from '@/utils'

// TODO: schema数据源中添加validata 校验字段以及transformat格式化字段
class SchemaState {
  @observable jsonState = {
    title: "Todo",
    type: "object",
    sortdata: ["description", "name", '*'],
    // template: Tpl,
    formOpt: {
      layout: "horizontal",
      labelCol: { span: 6 },
      wrapperCol: { span: 18 },
    },
    properties: {
      name: {
        type: "string",
        title: "名字",
        default: "kico",
        required: true,
        layout: {
          labelCol: { span: 4 },
          wrapperCol: { span: 14, "offset": 6 }
        }
      },
      description: {
        type: "string",
        title: "描述",
        minLength: 30,
        component: "textarea",
        className: "test"
      },
      otherinfo: {
        title: "其他信息",
        type: "object",
        sortdata: ["location", '*'],
        properties: {
          age: {type: "integer"},
          done: {
            type: "boolean",
            title: "完成",
            required: true,
            default: false
          },
          location: {
            type: 'string',
            title: '地理位置',
            enum: ['us', 'nordic', 'other'],
            enumNames: ['US', 'Nordic country', 'Other']
          },
          county: {
            type: 'string',
            title: '国家',
            enum: ['CN', 'other'],
            enumNames: ['中国', '其他'],
            props: { inline: true },
          },
          state: {
            type: 'string',
            condition: "location=nordic",
            title: 'State',
            enum: ['AL', 'AK', 'AZ'],
            enumNames: ['Alabama', 'Alaska', 'Arizona']
          }
        }
      }
    }
  }

  @observable formData = {}

  constructor() {
    autorun(() => console.log(this.jsonState));
  }

  @computed get schemaTree () {
    const {source: schema, prettyData: uiSchema} = new FormatSchema({...this.jsonState})
    console.warn({ schema, uiSchema })
    return { schema, uiSchema }
  }

  @action.bound
  update (source) {
    this.jsonState = source;
  }

  @action.bound
  getData(result) {
    this.formData = result;
  }
}

export default new SchemaState()
