import React from 'react'
import { Button, Icon } from 'antd'

export const MagentoArrayTheme: React.FC<any> = props => {
  const {
    items,
    className,
    onAddClick,
    canAdd,
    // 取 schema 的 minItems 字段
    schema: { minItems = Infinity }
  } = props

  return (
    <>
      <ul className={`${className} react-magento-ul`}>
        {
          items && items.map(({index, children, hasRemove, onDropIndexClick, onAddClick}) => (
            <li key={index} className="react-magento-li">
              {children}
              {hasRemove && index + 1 > minItems && (
                <Button onClick={onDropIndexClick(index)} shape="circle" type="danger" icon="close"/>
              )}
            </li>
          ))
        }
        {
          canAdd && (
            <li className="react-magento-li_add" key="react-magento-add">
              <Button onClick={onAddClick} type="primary" shape="circle" icon="plus"/>
            </li>
          )
        }
      </ul>
    </>
  )
}


