import React, { useState, useEffect } from 'react';
import { UnControlled as CodeMirror } from 'react-codemirror2';
import { Alert } from 'antd';
import { useSetState } from '@/Hooks';

import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/dracula.css';
import 'codemirror/mode/javascript/javascript';

// export interface JsonState<T> {
//   isValid: Boolean;
//   source: T;
// }

const options = (isEdit) => ({
  theme: isEdit ? 'dracula' : 'default',
  height: 'auto',
  viewportMargin: Infinity,
  mode: {
    name: 'javascript',
    json: true,
    statementIndent: 2
  },
  tabSize: 2,
  indentWithTabs: false,
  smartIndent: true,
  lineNumbers: isEdit,
  lineWrapping: true,
  readOnly: !isEdit
});

const checkValid = source => {
  try {
    return JSON.parse(source);
  } catch (e) {
    return false;
  }
};

// const JsonSchema = <T>(props) => {
//   const source = JSON.stringify(props.source, null, 2);
//   const [state, set] = useState<JsonState<T>>({
//     source,
//     isValid: checkValid(source)
//   })
const JsonSchema = props => {
  const source = JSON.stringify(props.source, null, 2);
  const initState = {
    source,
    isValid: checkValid(source)
  };
  const [state, setState] = useSetState(initState);

  useEffect(() => {
    setState(initState);
  }, []);

  const emitHandler = (editor, data, value) => {
    const { onChange } = props;
    const isValid = checkValid(value);
    setState({ isValid });
    isValid && !!onChange && onChange(isValid);
  };

  const renderOpt = options(props.isEdit)

  return (
    <>
      {!state.isValid ? (
        <Alert
          type="error"
          message="请输入正确的 JSON 格式"
          showIcon
          style={{ margin: '10px 0' }}
        />
      ) : null}
      <CodeMirror
        value={state.source}
        // onBeforeChange={(editor, data, value, next) => {
        //   // setState({ source: value })
        //   next()
        // }}
        onChange={emitHandler}
        options={renderOpt}
      />
    </>
  );
};

export default JsonSchema;
