import React from 'react';
import GenForm from '@/core/GenForm';
import { Button, Form } from 'antd'
import { MagentoArrayTheme, MagentoForm, MagentoFieldTheme, MagentoObjectTheme } from './theme'
import magentoWidget from './widget'
// import magentoTransformErrors from './transform-errors'
import { FormProps } from 'antd/lib/form/Form';
export interface AntdFormProps extends FormProps {
  schema: object,
  uiSchema?: object,
  onSubmit?: React.FormEventHandler<any>,
  onCancel?: React.FormEventHandler<any>,
  formData?: any,
  disableSubmit?: boolean,
  widgets?: object,
  fields?: object,
  onChange?:  React.FormEventHandler<any>,
  formContext?: object,
  liveValidate?: boolean,
  children?: JSX.Element,
  validate?: Function,
  className?: string,
  FieldTemplate?: any
}

const FormItem = Form.Item;
const FormFactory = MagentoForm as any
const formItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 20,
      offset: 6,
    },
  },
}

const AntdForm: React.FC<AntdFormProps> = props => {
  const {
    schema,
    onSubmit,
    uiSchema,
    disableSubmit = false,
    formData,
    onCancel,
    widgets,
    fields,
    onChange,
    formContext,
    liveValidate,
    children,
    validate,
    className,
    FieldTemplate
  } = props

  return (
    <FormFactory
      schema={schema}
      uiSchema={uiSchema}
      formData={formData}
      widgets={{ ...magentoWidget, ...widgets }}
      fields={{ ...fields }}
      ArrayFieldTemplate={MagentoArrayTheme}
      FieldTemplate={FieldTemplate || MagentoFieldTheme}
      ObjectFieldTemplate={MagentoObjectTheme}
      className={`react-magento ${className || ''}`}
      onSubmit={onSubmit}
      showErrorList={false}
      onChange={onChange}
      formContext={formContext}
      liveValidate={liveValidate}
      noHtml5Validate
      // transformErrors={transformErrors}
      validate={validate}
    >
      {
        children || (
          <FormItem  {...formItemLayout}>
            <Button type="primary" htmlType="submit" disabled={disableSubmit}>
              提交
            </Button>
            <Button style={{marginLeft: 20}} onClick={onCancel}>
              返回
            </Button>
          </FormItem>
        )
      }
    </FormFactory>
  );
}

export default AntdForm
