import React from 'react';
import { Tabs } from 'antd';
import { StickyContainer, Sticky } from 'react-sticky';
import FormSchema from '@/components/FormSchema';
import JsonSchema from '@/components/JsonSchema';

import { Content, Schema } from './style';

const { TabPane } = Tabs;

const renderTabBar = (props, DefaultTabBar) => (
  <Sticky bottomOffset={80}>
    {({ style }) => (
      <DefaultTabBar
        {...props}
        style={{ ...style, zIndex: 10, background: '#fff', top: 64 }}
      />
    )}
  </Sticky>
);

const FormCreate: React.FC = () => {
  return (
    <Content>
      <StickyContainer>
        <Schema as={Tabs} defaultActiveKey="1" renderTabBar={renderTabBar}>
          <TabPane tab="JSONSchema" key="1">
            <FormSchema type="jsonState"/>
          </TabPane>
          <TabPane tab="formData" key="2">
            <FormSchema type="formData"/>
          </TabPane>
        </Schema>
      </StickyContainer>
    </Content>
  );
};

export default FormCreate;
