import React from 'react'

export const MagentoObjectTheme: React.FC<any> = ({title, properties}) => {
  return (
    <fieldset>
      <legend>{title}</legend>
      {properties.map(prop => (
        <React.Fragment key={prop.content.key}>
          {prop.content}
        </React.Fragment>
      ))}
    </fieldset>
  )
}
