import React from 'react';
import ArrayField from 'react-jsonschema-form/lib/components/fields/ArrayField';

// getUiOptions:
// get all passed options from ui:widget, ui:options, and ui:<optionName>

// optionsList:
// 获取enum，oneOf，anyOf

import {
  getWidget,
  getUiOptions,
  optionsList,
  retrieveSchema,
  getDefaultRegistry
} from '.';

export default class GenArrayField extends ArrayField {
  renderMultiSelect() {
    const {
      schema,
      idSchema,
      uiSchema,
      formData,
      disabled,
      readonly,
      autofocus,
      onBlur,
      onFocus,
      registry = getDefaultRegistry(),
      rawErrors
    } = this.props;
    const items = this.props.formData;
    const { widgets, definitions = {}, formContext = {} } = registry;
    const itemsSchem = retrieveSchema(schema.items, definitions, formData);
    const enumOptions = optionsList(itemsSchem);
    const { widget = 'select', ...options } = {
      ...getUiOptions(uiSchema),
      enumOptions
    };
    const Widget = getWidget(schema, widget, widgets);
    return (
      <Widget
        id={idSchema && idSchema.$id}
        multiple
        onChange={this.onSelectChange}
        onBlur={onBlur}
        onFocus={onFocus}
        options={options}
        schema={schema}
        value={items}
        disabled={disabled}
        readonly={readonly}
        formContext={formContext}
        autofocus={autofocus}
        rawErrors={rawErrors}
      />
    );
  }
}
