import React from 'react';
import { Alert } from 'antd';
import ArrayField from './GenArrayField';
import StringField from './GenStringField';
import BooleanField from './GenBooleanField';
import SchemaField from './GenSchemaField';
// merge (schema, widget)
import { getWidget as __getWidget } from 'react-jsonschema-form/lib/utils';
// 导出对应的方法
export * from 'react-jsonschema-form/lib/utils';

function Error(msg) {
  return () => <Alert message={msg} type="error" showIcon />;
}

// 封装 __getWidget
export function getWidget(...args) {
  try {
    return __getWidget(...args);
  } catch (e) {
    return Error(e.toString());
  }
}

function TitleField(props) {
  console.log(props)
  const { id, title, required } = props;
  return (
    <legend id={id}>
      {title}
      {required ? '*' : null}
    </legend>
  );
}

export function getDefaultRegistry() {
  const fields = {
    ...require('react-jsonschema-form/lib/components/fields').default,
    ArrayField,
    StringField,
    BooleanField,
    TitleField,
    SchemaField
  };

  return {
    fields,
    widgets: require('react-jsonschema-form/lib/components/widgets').default
  };
}
