import R from 'ramda'
import { argumentPlaceholder } from '@babel/types';

export interface FormatSchemaInterface {

}

const DIVI_WORD = 'properties'

export class FormatSchema implements FormatSchemaInterface {
  public source: object = {}
  public prettyData: object = {}
  private pathId: Array<string> = []
  private Trans_Map: Map<string, string> = new Map([
    ["component", "ui:widget"],
    ["className", "classNames"],
    ["condition", "condition"],
    ["sortdata", "ui:order"],
    ["props", "ui:options"],
    // ["layout", "ui:options"],
  ])

  private keyHandler = (obj, arr, fn) => Object.keys(obj).forEach(k => arr.push(k) && fn(obj[k]))

  private pipeData = (source: object, prettyData: object, path: Array<String>) => (data: object) => {
    return R.compose(this.separate(source, path, data), this.filterData, Object.keys)(data)
  }

  // 组装数据
  private separate = (source, path, data) => arr => {
    // 获取当前所属分类
    const parentName = path.pop()

    this.prettyMap(this.prettyData, path, arr, parentName, data)
    this.formatSource(path, data, parentName, source)
  }

  // 如果有符合 Trans_Map 的字段
  private prettyMap = (prettyData, path, arr, parentName, data) => {
    if (!arr.length) return

    let renderNode = prettyData
    const formatArr = path.filter(x => x !== DIVI_WORD)

    const name = `${this.Trans_Map.get('sortdata')}`
    const initOrder = (this.source as any).sortdata
    initOrder && initOrder.length && (prettyData[name] = initOrder)

    formatArr.length && formatArr.reduce((r, n, k) => {
      let temp = this.source
      for (let x of path) {
        temp = temp[x]
        if (x === n) break
      }

      r[n] = r[n] || {}
      // add properties 之前的那个
      if (k === formatArr.length - 1) {
        const hasOrder = (temp as any).sortdata
        hasOrder && hasOrder.length && (r[n][name] = hasOrder)
        renderNode = r[n]
      }
      return r[n]
    }, prettyData)

    arr.forEach((property) => renderNode[parentName] = Object.assign(renderNode[parentName] || {}, {[(this.Trans_Map.get(property) as string)]: data[property]}))
  }

  // 为 source 添加新的属性
  private formatSource = (path, data, parentName, source) => {
    this.formatRequire(path, data, parentName, source)
  }


  private formatRequire = (path, data, parentName, source) => {
    const LEN = path.length - 1
    if (path[LEN] !== 'properties' || !data.required) return

    // 处理required
    path.reduce((r, n, k) => {
      if (!LEN) {
        r.required = r.required || []
        r.required.push(parentName)
      } else if (k === LEN - 1) {
        r[n] = Object.assign(r[n] || {}, {required: r[n].required || []})
        r[n].required.push(parentName)
      }
      return r[n]
    }, source)
  }

  private filterData = R.memoizeWith(R.identity, _ => _.filter(x => this.Trans_Map.has(x)))

  public constructor(source) {
    this.source = source
    this.init(this.source)
  }

  protected init = (data) => {
    if (data.hasOwnProperty(DIVI_WORD)) {
      this.pathId.push(DIVI_WORD)
      this.keyHandler(data.properties, this.pathId, this.init)
    } else {
      const name = Object.keys(data).find(x => data[x].hasOwnProperty(DIVI_WORD))
      name ? this.keyHandler(data[name].properties, this.pathId, this.init) : this.pipeData(this.source, this.prettyData, this.pathId)(data)
    }
  }
}
