import React from 'react'
import SchemaField from "react-jsonschema-form/lib/components/fields/SchemaField";
import GenForm from '@/core/GenForm';
import { Form } from 'antd'
import { FormProps } from 'antd/lib/form/Form';
import { GlobalStyle } from './style.js'

export interface AntdFormProps extends FormProps {
  schema: object,
  uiSchema?: object,
  onSubmit?: React.FormEventHandler<any>,
  onCancel?: React.FormEventHandler<any>,
  formData?: any,
  disableSubmit?: boolean,
  widgets?: object,
  fields?: object,
  onChange?:  React.FormEventHandler<any>,
  formContext?: object,
  liveValidate?: boolean,
  children?: JSX.Element,
  validate?: Function,
  className?: string,
}

export class MagentoForm extends GenForm {

  render() {
    const {
      children,
      safeRenderCompletion,
      id,
      idPrefix,
      className,
      name,
      method,
      target,
      action,
      autocomplete,
      enctype,
      acceptcharset,
      noHtml5Validate,
      disabled,
    } = (this as any).props;

    const { schema, uiSchema, formData, errorSchema, idSchema } = (this as any).state;
    const registry = this.getRegistry();
    const _SchemaField = registry.fields.SchemaField;
    let formLayout: FormProps = {layout: 'horizontal'}
    if (schema.hasOwnProperty("formOpt")) {
      formLayout = schema.formOpt
    }

    return (
      <>
        <GlobalStyle />
        <Form
          {...formLayout}
          className={className ? className : "rjsf"}
          id={id}
          name={name}
          method={method}
          target={target}
          action={action}
          autoComplete={autocomplete}
          encType={enctype}
          acceptCharset={acceptcharset}
          noValidate={noHtml5Validate}
          onSubmit={(this as any).onSubmit}
          ref={form => {
            (this as any).formElement = form;
          }}>
            {(this as any).renderErrors()}
            <_SchemaField
              schema={schema}
              uiSchema={uiSchema}
              errorSchema={errorSchema}
              idSchema={idSchema}
              idPrefix={idPrefix}
              formData={formData}
              onChange={(this as any).onChange}
              onBlur={(this as any).onBlur}
              onFocus={(this as any).onFocus}
              registry={registry}
              safeRenderCompletion={safeRenderCompletion}
              disabled={disabled}
            />
            {children ? (
              children
            ) : (
              <div>
                <button type="submit" className="btn btn-info">
                  Submit
                </button>
              </div>
            )}
        </Form>
      </>
    );
  }
}

