import React from 'react'
import { Form, Icon } from 'antd'
import EditExtra from '@/components/EditExtra'
const FormItem = Form.Item

/**
 * 当type = ['object', 'null'] || ‘object’ 的时候为true
 * @param {string|Array} type
 */
function isObjectType(type) {
  if (Array.isArray(type)) {
    return (
      type.length === 2 && type.includes('null') && type.includes('object')
    );
  }
  return type === 'object';
}

export const MagentoFieldTheme: React.FC<any> = (props) => {
  const {
    id,
    schema,
    uiSchema,
    children,
    label,
    required,
    rawHelp,
    rawErrors,
  } = props;

  let formItemLayout = null

  if (isObjectType(schema.type) && !uiSchema.hasOwnProperty('ui:field')) {
    return children
  }

  if (schema.hasOwnProperty("layout")) {
    formItemLayout = schema.layout
  }
  const validateStatus = rawErrors && rawErrors.length ? 'error' : 'success'
  const help = rawErrors && rawErrors.length ? rawErrors.join(';') : rawHelp

  return (
    <FormItem
      {...formItemLayout}
      label={label}
      required={required}
      help={help}
      validateStatus={validateStatus}
    >
      {children}
    </FormItem>
  )
}

