import React from 'react'
import { isStringNull, noop } from '@/utils'

/**
 * 高阶组件，将Fusion组件适配为ReactJsonschemaForm支持的格式
 * @param {React.Component} Widget - 需要包装的react组件
 * @param {Object} [options] - 除去以下特殊字段外，用于组件本身的预定义属性
 * @param {Function} [options.transformValue] - 组件的值转为满足jsonschema定义的值
 * @param {Function} [options.fromValue] - jsonschema form的值转为满足组件的值
 * @param {String} [options.valuePropName=value] - 组件用于接收value的字段名字，一般是`value`，但有例外，比如`Switch`用`checked`表示
 * @param {Object} [options.style] - 组件样式
 */
const withWidget = (Widget, {
  transformValue=noop,
  fromValue=noop,
  valuePropName='value',
  style={width: 300},
  ...restOption
} = {}): (props: any) => JSX.Element => {
  function customWidget(props) {
    const { value, onChange, placeholder, options, schema } = props
    const { enumOptions, width, schema_format, ...restProps } = options
    const widgetStyle = {
      ...style,
      ...(width !== undefined ? { width } : {})
    }
    const widgetProps = {
      ...restProps,
      style: widgetStyle,
      [valuePropName]: fromValue(value, schema, { schema_format }),
      placeholder,
      onChange: event => {
        const formatVal = transformValue(typeof event === 'object' ? event.target.value : event, schema, { schema_format })
        onChange(isStringNull(formatVal) ? undefined : formatVal)
      }
    }
    if (enumOptions && enumOptions.length > 0) {
      widgetProps.enumOptions = enumOptions
    }
    if (schema && schema.maxLength) {
      widgetProps.maxLength = schema.maxLength
    }
    if (!placeholder && schema.title) {
      widgetProps.placeholder = `请填写${schema.title}`
    }
    return <Widget {...restOption} {...widgetProps} />
  }
  return customWidget
}

export default withWidget
