import React, {useState} from 'react';
import { observer, inject } from 'mobx-react';
import { observable } from 'mobx';
import AntdForm from '@/core/antd';


const log = type => console.log.bind(console, type);

const Depict = inject('schemaState')(
  observer(store => {
    const [state, setState] = useState({})
    const changeHandler = (source) => {
      console.log('----', source)
      setState(source.formData)
      // store.schemaState.getData(observable(source.formData));
    };

    const schema = {
      type: 'object',
      formOpt: {
        layout: "horizontal",
        labelCol: { span: 6 },
        wrapperCol: { span: 18 },
      },
      properties: {
        title: {
          type: "string",
          title: "label 名称",
          required: true
        },
        type: {
          title: "组件类型",
          required: true,
          type: 'string',
          enum: ['input', 'textArea', 'switch', 'select', 'checkbox'],
          enumNames: ['input', 'textArea', 'switch', 'select', 'checkbox']
        },
        isRequire: {
          title: "是否必填",
          required: true,
          type: 'boolean'
        },
        options: {
          title: "选项",
          type: "array",
          items: {
            type: "string"
          }
        }
      }
    }


    return (
      <AntdForm
        schema={schema}
        formData={state}
        onChange={changeHandler}
        onSubmit={log('submitted')}
        onError={log('errors')}
      />
    )
  })
);

export default Depict;
