import React from 'react';
import { Menu, Icon } from 'antd';

// import { Content } from './style'

const { SubMenu } = Menu;

interface ChangeProps {
  onOpen?: (nextVal?: any) => void;
}

const JsonSchema: React.FC<ChangeProps> = (props: any) => {
  const handleClick = e => {
    console.log('click ', e);
    props.onOpen()
  };

  return (
    <Menu
      theme="dark"
      mode="inline"
      defaultSelectedKeys={['input']}
      onClick={handleClick}
      defaultOpenKeys={['antd']}
    >
      <SubMenu
        key="antd"
        title={
          <span>
            <Icon type="mail" />
            <span>Antd 风格</span>
          </span>
        }
      >
        <Menu.ItemGroup key="form" title="表单系列">
          <Menu.Item key="input">Input</Menu.Item>
          <Menu.Item key="button">Button</Menu.Item>
        </Menu.ItemGroup>
      </SubMenu>
    </Menu>
  );
};

export default JsonSchema;
