import React from 'react';
import Form from 'react-jsonschema-form/lib';
import { getDefaultRegistry } from '.';

class GenForm extends Form {
  getRegistry() {
    const {
      fields: defaultFields,
      widgets: defaultWidgets
    } = getDefaultRegistry();
    const {
      fields,
      widgets,
      ArrayFieldTemplate,
      ObjectFieldTemplate,
      FieldTemplate,
      schema,
      formContext
    } = (this as any).props;

    return {
      fields: { ...defaultFields, ...fields },
      widgets: { ...defaultWidgets, ...widgets },
      definitions: { ...schema.definitions },
      formContext: { ...formContext },
      ArrayFieldTemplate,
      ObjectFieldTemplate,
      FieldTemplate
    };
  }
}

export default GenForm;
